figure {
  width: 6.25em;
  height: 6.25em;
  margin: auto;
  animation: rotate 2.4s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.white {
  opacity: 0;
  background: #fff;
  animation: flash 2.4s linear infinite;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.dot {
  width: 2.4em;
  height: 2.4em;
  border-radius: 100%;
  margin: auto;
  transition: all 1s;
  position: absolute;
}

.dot:nth-child(2) {
  background: #f44;
  animation: dotsY 2.4s linear infinite;
  top: 0;
  bottom: 0;
  left: 0;
}

.dot:nth-child(3) {
  background: #fb3;
  animation: dotsX 2.4s linear infinite;
  top: 0;
  left: 0;
  right: 0;
}

.dot:nth-child(4) {
  background: #9c0;
  animation: dotsY 2.4s linear infinite;
  top: 0;
  bottom: 0;
  right: 0;
}

.dot:nth-child(5) {
  background: #33b5e5;
  animation: dotsX 2.4s linear infinite;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  10% {
    width: 6.25em;
    height: 6.25em;
  }

  66% {
    width: 2.4em;
    height: 2.4em;
  }

  100% {
    width: 6.25em;
    height: 6.25em;
    transform: rotate(360deg);
  }
}

@keyframes dotsY {
  66% {
    opacity: .1;
    width: 2.4em;
  }

  77% {
    opacity: 1;
    width: 0;
  }
}

@keyframes dotsX {
  66% {
    opacity: .1;
    height: 2.4em;
  }

  77% {
    opacity: 1;
    height: 0;
  }
}

@keyframes flash {
  33% {
    opacity: 0;
    border-radius: 0%;
  }

  55% {
    opacity: .6;
    border-radius: 100%;
  }

  66% {
    opacity: 0;
  }
}

